#header {
    align-items: center;
    margin: 100px auto 50px auto;
    text-align: center;
    font-size: large;
}
.navbar__logo img {
    flex: 1 1;
    height: 50px;
}

.navbar {
    overflow: hidden;
    top: 0;
    position: fixed;
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 20px 0 20px;
    background-color: #b1271d;
    box-shadow: 0 1px 2px rgba(0, 0, 0, 0.1);
    z-index: 1;
}

.navbar a:hover {
  color: black;
}
.navbar__navbar-links {
    display: flex;
    justify-content: space-around;
    align-items: center;
    flex: 1 1;
}

a {
    text-decoration: none;
    font-size: 1.5rem;
    margin: 0 10px;
    transition: all 0.3s ease-in;
}

table {
    margin: 0px auto 0px auto;
}

th, td {
    padding: 15px;
}
